import { ajax } from '@/static/js/base.js';
import api from '@/static/js/api.js';
import { mapGetters } from 'vuex';
export default {
	data() {
		return {
			info: {},
			typeId: '',
			current: 0
		};
	},
	onLoad(e) {
		this.id = e.id;
		ajax({
			url: api.getInvestmentInfo,
			type: 'POST',
			data: {
				id: this.id
			}
		}).then(res => {
			if (res.status_code == 'ok') {
				console.log(res);
				this.info = res.data;
			}
		});
	},
	methods: {
		getDetail(e) {
			this.typeId = e;
			ajax({
				url: api.getInvestmentDetails,
				type: 'POST',
				data: {
					id: this.id
				}
			}).then(res => {
				if (res.status_code == 'ok') {
					console.log(res);
					this.info.mobile = res.data.mobile;
				} else if (res.status_code == 'error') {
					this.$refs.popup.open();
				} else {
					uni.showToast({
						title: res.message,
						icon: 'none'
					});
				}
			});
		},
		close() {
			this.$refs.popup.close();
		},
		navMembership() {
			uni.navigateTo({
				url: '../posTinvestor/posTinvestor'
			});
			this.$refs.popup.close();
		},
		postMeaasge(e) {
			this.typeId = e;
			this.$refs.popup.open();
		},
		inputValue(e) {
			var len = parseInt(e.detail.value.length);
			// if (len > 200) return;
			console.log(len)
			this.content = e.detail.value
			this.current = len
		},
		post(){
			ajax({
				url: api.addMessage,
				type: 'POST',
				data: {
					content: this.content
				}
			}).then(res => {
				if (res.status_code == 'ok') {
					console.log(res);
					uni.showToast({
						title: res.message,
						icon: 'none'
					});
					this.$refs.popup.close();
					// this.info.mobile = res.data.mobile;
				}else {
					uni.showToast({
						title: res.message,
						icon: 'none'
					});
				}
			});
		}
	}
};
